import React from 'react'
import {
  ifElse,
  __,
  equals,
  replace,
  map,
  hasPath,
  allPass,
  not,
  always,
  invoker,
  is,
  head,
  path,
  identity,
  find,
  propEq,
  pathOr,
} from 'ramda'
import { Row, Col, Form, Tooltip, Spin, message } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { EyeOutlined, EditOutlined } from '@ant-design/icons'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb } from '@/hooks/useApp'
import useFormValues from '@/hooks/useFormValues'
import { Report, useReports } from '@/services/report'
import Select from '@/components/select'
import Input from '@/components/input'
import { RangePicker } from '@/components/datepicker'
import { Page } from '@/components/layout'
import Table from '@/components/query-table'
import Button from '@/components/button'
import history from '@/utils/history'
import { reportStatusEnums } from '@/constants/enums'
import { REPORT_EDIT, MEDICAL_ORDER, REPORT_INQUIRY } from '@/constants/routes'
import { createFormItemLayout, rules } from '@/utils/form'
import { sorter } from '@/utils/webHelper'
import env from '@/constants/env'
import FormItem from 'antd/lib/form/FormItem'
const { compose } = require('ramda')

const formItemLayout = createFormItemLayout(6)

export default function ReportList() {
  useTitle('報告查詢')

  const setBreadcrumb = useBreadcrumb([{ name: '報告查詢' }])

  // const [noType, setNoType] = React.useState<string | undefined>()
  const [shouldRedirect, setShouldRedirect] = React.useState(false)

  const [form] = Form.useForm()

  const transform = useFormValues({ format: 'YYYY-MM-DD' })

  const { loading, data, refetch, called } = useReports({
    onCompleted: data => {
      setShouldRedirect(false)

      if (!shouldRedirect) {
        return
      }

      const response = ifElse(is(Array), head, identity)(data)

      const hasTemplate = hasPath(['templateId'])
      const isFinish = compose(equals(__, '完成'), path(['orstatus']))
      const hasORReqno = hasPath(['orreqno'])

      if (allPass([hasORReqno, hasTemplate, isFinish])(response)) {
        history.push(
          `${replace(':id', response.orreqno)(REPORT_EDIT)}?finish=1`
        )
      }
      if (allPass([hasORReqno, hasTemplate])(response)) {
        history.push(replace(':id', response.orreqno)(REPORT_EDIT))
      }
      if (allPass([hasORReqno, compose(not, hasTemplate)])(response)) {
        history.push(`${REPORT_INQUIRY}?orreqno=${response.orreqno}`)
      }
      if (compose(not, hasPath(['orreqno']))(response)) {
        message.error('查無報告')
      }
    },
  })

  const handleOk = () => {
    form.validateFields().then(async values => {
      // 已完成 要跳到輸出報告頁面
      // 未輸入 要跳到編輯報告頁面
      if (values.classification === 'rpresult') {
        setShouldRedirect(true)
      }

      refetch(
        transform({
          ...values,
          // noType,
        })
      )
      setBreadcrumb([{ name: '報告查詢' }, { name: '查詢結果' }])
    })
  }

  // const handleNoTypeChange = (value?: string) => {
  //   setNoType(value)
  // }

  const handleToReport = (evt: React.MouseEvent<HTMLElement>) => {
    const report = compose(
      find(propEq('orreqno', evt.currentTarget.dataset.id)),
      pathOr([], ['content'])
    )(data)

    if (!report) {
      return
    }

    const isFinish = compose(equals(__, '完成'), path(['orstatus']))
    const isUntype = compose(equals(__, '未輸'), path(['orstatus']))
    const isTyped = compose(equals(__, '已輸'), path(['orstatus']))
    const hasORReqno = hasPath(['orreqno'])

    if (allPass([hasORReqno, isFinish])(report)) {
      history.push(`${replace(':id', report.orreqno)(REPORT_EDIT)}?finish=1`)
    }
    if (allPass([hasORReqno, isTyped])(report)) {
      history.push(replace(':id', report.orreqno)(REPORT_EDIT))
    }
    if (allPass([hasORReqno, isUntype])(report)) {
      history.push(`${REPORT_INQUIRY}?orreqno=${report.orreqno}`)
    }
  }

  const handleToImageViewer = (evt: React.MouseEvent<HTMLElement>) => {
    const url = replace(':id', '')(env.viewerUrl)
    window.open(`${url}?PatientID=${evt.currentTarget.dataset.id}`, '__blank')
  }

  const handleToMedicalOrder = (evt: React.MouseEvent<HTMLElement>) => {
    history.push(
      replace(':id', `${evt.currentTarget.dataset.id}`)(MEDICAL_ORDER)
    )
  }

  const handleResetFields = () => form.resetFields()

  const columns: ColumnProps<Report>[] = [
    { title: '申請序號', dataIndex: 'orreqno', sorter: sorter('orreqno') },
    { title: '姓名', dataIndex: 'hnamec' },
    { title: '病理號', dataIndex: 'orworkno', sorter: sorter('orworkno') },
    { title: '病歷號', dataIndex: 'hhisnum', sorter: sorter('hhisnum') },
    { title: '病房', dataIndex: 'hnursta', sorter: sorter('hnursta') },
    { title: '床號', dataIndex: 'hbed', sorter: sorter('hbed') },
    { title: '性別', dataIndex: 'hsex', sorter: sorter('hsex') },
    { title: '簽收日期', dataIndex: 'zrsregord', sorter: sorter('zrsregord') },
    { title: '收費碼', dataIndex: 'ordclnm' },
    { title: '科別', dataIndex: 'csect', sorter: sorter('csect') },
    { title: '狀態', dataIndex: 'orstatus', sorter: sorter('orstatus') },
    {
      title: '醫囑',
      dataIndex: 'ORPROCED',
      render: (_, r) => (
        <Tooltip title="查看醫囑內容">
          <EyeOutlined
            data-id={r.orreqno}
            className="cursor-pointer text-lg"
            onClick={handleToMedicalOrder}
          />
        </Tooltip>
      ),
    },
    {
      title: '報告',
      dataIndex: 'x',
      render: (_, r) => (
        <Tooltip title="編輯、查看報告內容">
          <EditOutlined
            data-id={r.orreqno}
            className="cursor-pointer text-lg"
            onClick={handleToReport}
          />
        </Tooltip>
      ),
    },
    {
      title: '影像',
      dataIndex: 'createAt',
      render: (_, r) => (
        <Tooltip title="開啟新視窗查看影像">
          <EyeOutlined
            data-id={r.hhisnum}
            className="cursor-pointer text-lg"
            onClick={handleToImageViewer}
          />
        </Tooltip>
      ),
    },
  ]

  const renderFormField = (children: any) => {
    return (
      <Form.Item noStyle shouldUpdate>
        {({ getFieldValue }) => {
          return children({
            disabled: getFieldValue('classification') === 'rpresult',
          })
        }}
      </Form.Item>
    )
  }

  return (
    <Spin spinning={loading}>
      <Page style={{ minHeight: 'calc(100vh - 203px)' }}>
        <Row justify="center">
          <Col md={22} xs={24}>
            <Form
              form={form}
              layout="horizontal"
              hideRequiredMark={true}
              labelAlign="left"
              colon={false}>
              <Row gutter={12} justify="space-between">
                <Col md={7} xs={24}>
                  <FormItem noStyle shouldUpdate>
                    {form => {
                      const _rules = compose(
                        ifElse(
                          equals(__, 'rpresult'),
                          always({ rules: [rules.required] }),
                          always({})
                        ),
                        invoker(1, 'getFieldValue')('classification')
                      )(form)

                      return (
                        <Form.Item
                          {..._rules}
                          labelCol={{ span: 5 }}
                          wrapperCol={{ span: 19 }}
                          label="查詢單號"
                          name="no">
                          <Input
                            addonBefore={
                              <Form.Item noStyle name="noType">
                                <Select
                                  {..._rules}
                                  allowClear
                                  style={{ width: 100 }}
                                  placeholder="單號類型">
                                  <Select.Option value="ORREQNO">
                                    申請序號
                                  </Select.Option>
                                  <Select.Option value="ORWORKNO">
                                    病理號
                                  </Select.Option>
                                  <Select.Option value="HHISNUM">
                                    病歷號
                                  </Select.Option>
                                </Select>
                              </Form.Item>
                            }
                          />
                        </Form.Item>
                      )
                    }}
                  </FormItem>
                </Col>
                <Col md={5} xs={24}>
                  <Form.Item
                    {...formItemLayout}
                    label="查詢類別"
                    name="classification"
                    rules={[rules.required]}>
                    <Select allowClear>
                      <Select.Option value="pathorderview">醫囑</Select.Option>
                      <Select.Option value="rpresult">報告</Select.Option>
                    </Select>
                  </Form.Item>
                </Col>
                <Col md={6} xs={24}>
                  {renderFormField(({ disabled }: any) => (
                    <Form.Item {...formItemLayout} label="簽收日期" name="date">
                      <RangePicker className="w-full" disabled={disabled} />
                    </Form.Item>
                  ))}
                  {/* <Form.Item noStyle shouldUpdate>
                    {({ getFieldValue }) => {
                      const disabled =
                        getFieldValue('classification') === 'rpresult'
                      return (
                        <Form.Item
                          {...formItemLayout}
                          label="簽收日期"
                          name="date">
                          <RangePicker className="w-full" disabled={disabled} />
                        </Form.Item>
                      )
                    }}
                  </Form.Item> */}
                </Col>
                <Col md={6} xs={24}>
                  {renderFormField(({ disabled }: any) => (
                    <Form.Item
                      {...formItemLayout}
                      label="報告狀態"
                      name="reportStatus">
                      <Select allowClear disabled={disabled}>
                        {map((x: { id: number; name: string }) => (
                          <Select.Option key={x.id} value={x.id}>
                            {x.name}
                          </Select.Option>
                        ))(reportStatusEnums)}
                      </Select>
                    </Form.Item>
                  ))}
                  {/* <Form.Item shouldUpdate noStyle>
                    {({ getFieldValue }) => {
                      const disabled =
                        getFieldValue('classification') === 'rpresult'
                      return (
                        <Form.Item
                          {...formItemLayout}
                          label="報告狀態"
                          name="reportStatus">
                          <Select allowClear disabled={disabled}>
                            {map((x: { id: number; name: string }) => (
                              <Select.Option key={x.id} value={x.id}>
                                {x.name}
                              </Select.Option>
                            ))(reportStatusEnums)}
                          </Select>
                        </Form.Item>
                      )
                    }}
                  </Form.Item> */}
                </Col>
              </Row>
              <Row className="mt-4">
                <Col md={24} xs={24} className="flex justify-end">
                  <Form.Item>
                    <Button
                      color="primary"
                      style={{ width: 96 }}
                      className="mr-3"
                      onClick={handleResetFields}>
                      重設
                    </Button>
                    <Button
                      color="primary"
                      style={{ width: 96 }}
                      onClick={handleOk}>
                      搜尋
                    </Button>
                  </Form.Item>
                </Col>
              </Row>
            </Form>
          </Col>
        </Row>
        {called && !shouldRedirect && (
          <>
            <Row className="mb-2 mt-4" justify="center">
              <Col md={24} xs={24} className="text-sm">
                檢索結果共 <span className="text-primary">{data.total}</span> 筆
              </Col>
            </Row>
            <Row justify="center">
              <Col md={24} xs={24}>
                <Table rowKey="id" dataSource={data} columns={columns} />
              </Col>
            </Row>
          </>
        )}
      </Page>
    </Spin>
  )
}
