import { evolve, ifElse, isNil, identity, invoker } from 'ramda'
import { getRangeDate } from '@/utils/webHelper'

export default function useFormValues({
  startDateField = 'startDate',
  endDateField = 'endDate',
  format,
}: {
  format?: string
  startDateField?: string
  endDateField?: string
}) {
  return (values: { [key: string]: any }) => {
    if (!values) {
      return {}
    }

    const { date = [], ...rest } = values

    const [startDate, endDate] = getRangeDate(date, format)

    return evolve({
      createDate: ifElse(isNil, identity, invoker(1, 'format')('YYYY-MM-DD')),
      reportDate: ifElse(isNil, identity, invoker(1, 'format')('YYYY-MM-DD')),
    })({
      ...rest,
      [startDateField]: startDate,
      [endDateField]: endDate,
    })
  }
}
